.all_pizzas{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100vh;
}

.search-and-display{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 70%;
}

.form_pizza{
    width: 30%;
    background-color: brown;
}

.div-search-bar{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 2px;
    margin-bottom: 2px;
    width: 100%;
}

.label-search-bar{
    font-size: 20px;
}

.div-input-search-bar{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 5px;
    margin-top: 5px;
}

.input-search-bar{
    width: 70%;
    border: 1px solid #ccc;
    border-radius: 4px;
    height: 40px;
}


.div-display-pizzas-ingredient{
    overflow-y: scroll;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 90%;
    margin-top: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.div-card-pizzas-ingredient{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 5px;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    width: 30%;
}

.div-display-pizzas{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.div-display-ingredient{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

